import React, { useEffect } from "react";
import { IoRestaurant } from "react-icons/io5";
import { Formik, Form, ErrorMessage, Field } from "formik";
import { loginSchema } from "../../util/Schema";
import { useDispatch, useSelector } from "react-redux";
import { authState, userLogin } from "../../redux/slices/AuthSlice";
import toast, { Toaster } from "react-hot-toast";
import { useNavigate } from "react-router-dom";
import LineSpinner from "../customLoader/LineSpinner";

const Login = () => {
  const { status, code } = useSelector(authState);
  const dispatch = useDispatch();
  const navigate = useNavigate();

  useEffect(() => {
    if (code === 200) {
      toast.success(status);
    } else if ((code === 403 || code === 500) && status !== "loading") {
      toast.error(status);
    }
  }, [status, code]);

  useEffect(() => {
    if (sessionStorage.getItem("token")) {
      navigate("/home");
    }
  }, [sessionStorage.getItem("token")]);
  return (
    <>
      <Toaster />
      {status === "loading" && <LineSpinner />}
      <div className="relative bg-loginBackgroundImage min-h-screen bg-no-repeat bg-cover bg-center text-2xl w-screen blur-sm"></div>
      <div
        className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 border-2 rounded-2xl backdrop-blur-xl backdrop-brightness-105 backdrop-saturate-150
      flex justify-center items-center flex-col px-20 py-10 md:px-24 md:py-12 gap-2 shadow-xl"
      >
        <div className=" mb-2">
          <span className=" inline-flex  gap-1 text-orange-500 font-extrabold text-3xl">
            <IoRestaurant />
            COCO
          </span>
        </div>
        <div className=" text-center">
          <h2 className=" text-2xl md:text-4xl  font-bold py-2">Login</h2>
          <p className=" text-base md:text-lg pb-2 text-justify md:text-center">
            Lorem ipsum dolor sit amet consectetur adipisicing elit. Maiores
            dicta beatae rem officia quos cum laboriosam quaerat atque
            voluptatum voluptates.
          </p>
        </div>
        <div className="flex flex-col gap-3">
          <Formik
            initialValues={{ username: "", password: "" }}
            validationSchema={loginSchema}
            onSubmit={(values, action) => {
              dispatch(userLogin(values));
              action.setSubmitting(false);
              action.resetForm({
                values: {
                  username: "",
                  password: "",
                },
              });
            }}
          >
            {(props) => (
              <>
                <Form className=" w-96">
                  <div className="mb-3">
                    <Field
                      name="username"
                      placeholder="Username"
                      maxLength={25}
                      className=" w-full focus:outline-none px-16 py-2 md:px-20 border rounded-2xl border-gray-400 focus:border-orange-500"
                    />

                    <div className=" text-red-600 text-sm font-light">
                      <ErrorMessage name="username"></ErrorMessage>
                    </div>
                  </div>
                  <div className="mb-3">
                    <Field
                      type="password"
                      name="password"
                      maxLength={20}
                      className=" w-full focus:outline-none px-16 py-2 md:px-20 border  rounded-2xl border-gray-400 focus:border-orange-500"
                      placeholder="Password"
                    />
                    <div className=" text-red-600 text-sm font-light break-words">
                      <ErrorMessage name="password"></ErrorMessage>
                    </div>
                  </div>
                  {/* <div className="text-start px-2 pb-2 font-medium text-orange-500">
                    <a href="" className="hover:text-gray-600">
                      Forget Password?
                    </a>
                  </div> */}
                  <div className=" grid ">
                    <button
                      disabled={props.isSubmitting}
                      type="submit"
                      className="rounded-2xl py-2 px-2 bg-orange-500 text-white hover:opacity-85 transition ease-in-out delay-75"
                    >
                      Running Order
                    </button>
                  </div>
                </Form>
              </>
            )}
          </Formik>
        </div>
      </div>
    </>
  );
};

export default Login;
